import React from 'react';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';
import { GOOGLE_MAP_API_KEY } from 'src/constants';
import { AddressIcon } from 'src/assets/icons';
import MaterialIcons from 'react-native-vector-icons/dist/MaterialIcons';

import MarkerIcon from '../../assets/icons/icon-marker.svg';
import './Map.scss';

class MapContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeMarker: {},
      selectedBranch: {},
      selectedMarkerBranch: {},
      locations: [],
      openModal: false,
    };
    this.listBranchRef = React.createRef();
  }

  componentDidMount() {
    document.addEventListener('mousedown', this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  handleClickOutside = (event) => {
    console.log(
      'handleClickOutside',
      this.state.openModal,
      !this.listBranchRef?.current?.contains(event.target)
    );
    if (
      this.state.openModal &&
      this.listBranchRef &&
      !this.listBranchRef?.current?.contains(event.target)
    ) {
      this.setState({ openModal: false });
    }
  };

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.locations !== prevState.locations) {
      return {
        selectedBranch:
          nextProps.locations && nextProps.locations.length > 0
            ? nextProps.locations[0]
            : {},
        locations: nextProps.locations,
      };
    }
    return null;
  }

  onMarkerClick = (props, marker) => {
    this.setState({
      activeMarker: marker,
      selectedMarkerBranch: props.position,
      selectedBranch: props.position,
    });
  };

  render() {
    const {
      activeMarker,
      openModal,
      selectedBranch,
      selectedMarkerBranch,
    } = this.state;
    return (
      <div className="list_branch_map_container">
        <div
          onClick={() => {
            this.setState({
              openModal: !openModal,
            });
          }}
          ref={this.listBranchRef}
          className="suggestion_branch_container"
        >
          <div style={{ marginLeft: '12px' }}>
            <AddressIcon />
          </div>
          <div className="selected_branch_text">{selectedBranch.name}</div>
          <MaterialIcons name="arrow-drop-down" color="#3A556A" size={30} />
          {openModal && (
            <div className="list_branch_modal_container">
              {this.props.locations.map((obj) => (
                <div
                  onClick={() => {
                    this.setState({
                      selectedBranch: obj,
                    });
                  }}
                  className="branch_name_text"
                >
                  {obj.name}
                </div>
              ))}
            </div>
          )}
        </div>
        <div className="map_container">
          {this.props.locations &&
            this.props.locations.length > 0 &&
            this.props.locations[0].lat && (
              <Map
                google={this.props.google}
                zoom={14}
                center={this.state.selectedBranch}
                initialCenter={this.props.locations[0]}
                style={{ width: '100%', height: '400px', position: 'relative' }}
              >
                {this.props.locations.map((obj) => (
                  <Marker
                    position={obj}
                    onClick={this.onMarkerClick}
                    icon={{ url: MarkerIcon }}
                  />
                ))}
                <InfoWindow marker={activeMarker} visible>
                  <div>
                    <div className="title_marker">
                      <img
                        className="image_merchant_marker"
                        alt=""
                        src={selectedMarkerBranch?.image_url}
                      />
                      <div className="merchant_name_marker">
                        {selectedMarkerBranch.name}
                      </div>
                    </div>
                    <div className="merchant_address_marker">
                      {selectedMarkerBranch.address}
                    </div>
                  </div>
                </InfoWindow>
              </Map>
            )}
        </div>
      </div>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: GOOGLE_MAP_API_KEY,
})(MapContainer);
