/* eslint-disable react/jsx-props-no-spreading */
import React, { useRef } from 'react';
import {
  Grid,
  withStyles,
  Box,
  Tab,
  Tabs,
  makeStyles,
  Hidden,
} from '@material-ui/core';
import ReactMarkdown from 'react-markdown';
import { Breadcrumb, ButtonCustom } from 'src/components/base';
import { FBCircleIcon, GGCircleIcon, TWCircleIcon } from 'src/assets/icons';
import { useHistory } from 'react-router-dom';

import Payment from '../../../../components/Payment';
import VoucherSuggestionItem from '../../../../components/base/layout/VoucherSuggestionItem';
import Map from '../../../../components/Map/Map';

import './bookingDetail.scss';
import SlideShow from '../../../../components/SlideShow/SlideShow';
// import Map from '../../../../components/Map/Map';

const a11yProps = (index) => {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  tab: {
    fontFamily: 'Google Sans',
    fontSize: '15px',
    fontWeight: '600',
  },
  rootTab: {
    fontFamily: 'Google Sans',
    fontSize: '15px',
    fontWeight: '600',
    color: '#000',
    backgroundColor: '#fff',
  },
}));

const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    height: '4px',
    backgroundColor: '#fff',
    '& > span': {
      maxWidth: '100px',
      width: '100%',
      height: '5px',
      backgroundColor: '#EABFB9',
    },
  },
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <span /> }} />);
const BookingDetailView = (props) => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const history = useHistory();
  const tabContent1 = useRef();
  const tabContent2 = useRef();
  const tabContent3 = useRef();

  const handleChangeTab = (event, tabIndex) => {
    if (tabIndex === 0) {
      window.scrollTo(0, tabContent1.current.offsetTop);
    } else if (tabIndex === 1) {
      window.scrollTo(0, tabContent2.current.offsetTop);
    } else {
      window.scrollTo(0, tabContent3.current.offsetTop);
    }
    setValue(tabIndex);
  };

  const renderContent = (content) => {
    return (
      <div style={{ margin: '10px 20px' }}>
        <ReactMarkdown source={content} />
      </div>
    );
  };
  const voucher = props.voucher || {};
  return (
    <main
      style={{ backgroundColor: '#F6F9FF' }}
      className="booking-detail-wrapper"
    >
      <Breadcrumb
        items={[
          { name: 'Đặt bàn', url: '/booking' },
          { name: voucher.name, url: `/booking/${voucher.id}` },
        ]}
      />
      <Grid container className="booking-detail-container" spacing={3}>
        <Grid
          lg={8}
          xl={8}
          md={8}
          sm={12}
          xs={12}
          className="container-left"
          item
        >
          <div className="voucher_name_container">
            <img
              src={voucher?.merchant?.image_url}
              alt="logo"
              className="img-logo-home"
            />
            <p className="voucher_name">{voucher?.name}</p>
          </div>
        </Grid>
        <Grid lg={4} xl={4} md={4} sm={12} xs={12} item>
          <div />
        </Grid>
      </Grid>
      <Grid container className="booking-detail-container" spacing={3}>
        <Grid
          lg={8}
          xl={8}
          md={7}
          sm={12}
          xs={12}
          className="container-left"
          item
        >
          {voucher.imageUrls && voucher.imageUrls.length > 0 && (
            <SlideShow
              customImageStyle="slide_image_show"
              items={voucher.imageUrls}
            />
          )}
          <div className="social_container">
            <p className="share_title">Chia sẻ</p>
            <div style={{ marginLeft: '18px' }}>
              <FBCircleIcon />
            </div>
            <div style={{ marginLeft: '18px' }}>
              <GGCircleIcon />
            </div>
            <div style={{ marginLeft: '18px' }}>
              <TWCircleIcon />
            </div>
          </div>
          <div className={classes.root}>
            <StyledTabs
              value={value}
              onChange={handleChangeTab}
              aria-label="simple tabs example"
              variant="fullWidth"
            >
              <Tab
                style={{ textTransform: 'none' }}
                classes={{
                  selected: classes.tab,
                  root: classes.rootTab,
                }}
                label="Điều kiện áp dụng"
                {...a11yProps(0)}
              />
              <Tab
                style={{ textTransform: 'none' }}
                classes={{
                  selected: classes.tab,
                  root: classes.rootTab,
                }}
                label="Thông tin chi tiết"
                {...a11yProps(1)}
              />
              <Tab
                style={{ textTransform: 'none' }}
                classes={{
                  selected: classes.tab,
                  root: classes.rootTab,
                }}
                label="Bản đồ địa chỉ áp dụng"
                {...a11yProps(2)}
              />
            </StyledTabs>
            <Box
              display="flex"
              style={{ backgroundColor: 'transparent', marginTop: '32px' }}
            >
              <div
                style={{
                  width: '4px',
                  backgroundColor: '#EABFB9',
                  marginTop: '5px',
                  marginBottom: '5px',
                  marginRight: '20px',
                }}
              />
              <p className="map_title" ref={tabContent1}>
                Điều kiện áp dụng
              </p>
            </Box>
            <div className="tab_content">
              {renderContent(props.voucher.condition)}
            </div>
            <Box display="flex" style={{ backgroundColor: 'transparent' }}>
              <div
                style={{
                  width: '4px',
                  backgroundColor: '#EABFB9',
                  marginTop: '5px',
                  marginBottom: '5px',
                  marginRight: '20px',
                }}
              />
              <p className="map_title" ref={tabContent2}>
                Thông tin chi tiết
              </p>
            </Box>
            <div className="tab_content">
              {renderContent(props.voucher.description)}
            </div>

            <Box display="flex" style={{ backgroundColor: 'transparent' }}>
              <div
                style={{
                  width: '4px',
                  backgroundColor: '#EABFB9',
                  marginTop: '5px',
                  marginBottom: '5px',
                  marginRight: '20px',
                }}
              />
              <p className="map_title" ref={tabContent3}>
                Bản đồ địa chỉ áp dụng
              </p>
            </Box>
            <Map
              // eslint-disable-next-line react/prop-types
              // google={this.props.google}
              locations={voucher.branches}
              // height={420}
              // zoom={15}
              // isAutoComplete={false}
              apiKey="AIzaSyAEdKiEuvazphWXEMKBT4BIgsAFPaE3AgQ"
            />
            {/* <Map
              // eslint-disable-next-line react/prop-types
              // google={this.props.google}
              center={{ lat: branch.lat, lng: branch.lng }}
              // height={420}
              // zoom={15}
              // isAutoComplete={false}
              apiKey={'AIzaSyAEdKiEuvazphWXEMKBT4BIgsAFPaE3AgQ'}
            /> */}
          </div>
        </Grid>

        <Grid lg={4} xl={4} md={5} sm={12} xs={12} item>
          <div className="payment">
            <Payment
              showPrice={false}
              title="Đặt bàn"
              branches={voucher.branches}
              onClick={props.onBookingClick}
            />
          </div>
          <div className="merchant_info">
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <img
                className="merchant_logo"
                src={voucher?.merchant?.image_url}
                alt="logo"
              />
              <p className="merchant_name">{voucher?.merchant?.name}</p>
            </div>

            <p className="merchant_description">
              {voucher?.merchant?.description}
            </p>
          </div>

          <div className="promotion-section">
            <p className="promotion-title">Món ăn đề xuất</p>
            {props.suggestVoucher?.map((voucherDetail) => (
              <VoucherSuggestionItem
                onClick={() => {
                  if (voucherDetail.type_deal === 1) {
                    history.push(`/booking/${voucherDetail.id}`);
                  }
                  if (voucherDetail.type_deal === 2) {
                    history.push(`/voucher/${voucherDetail.id}`);
                  }
                  if (voucherDetail.type_deal === 3) {
                    history.push(`/delivery/${voucherDetail.id}`);
                  }
                }}
                deal={voucherDetail}
              />
            ))}
          </div>
        </Grid>
      </Grid>
      <Hidden smUp>
        <div className="button_booking_container">
          <ButtonCustom
            onClick={props.onBookingClick}
            title="Đặt bàn"
            customStyle="button_booking"
          />
        </div>
      </Hidden>
    </main>
  );
};

export default React.memo(BookingDetailView);
